body, h1, h2, h3, h4, h5, h6, p, ul, ol, table, dl, dd {
	margin: 0;
	padding: 0;
}
h1 {
	font-size: 16px;
}
a {
	text-decoration: none;
	color: #000;
}
ul, ol {
	list-style: none;
}
img {
	max-width: 100%;
	vertical-align: top;
	border: none;
}
body {
	min-width: 320px;
	line-height: 1;
	font-family: "Roboto", "Noto Sans KR", sans-serif;
	font-size: 1em;
	background-color: #222;
}
body.fixed {
	overflow-y: hidden;
}
/*header*/
#header {
	position: fixed;
	z-index: 1000;
	left: 0;
	top: 0;
	padding: 30px;
	width: 100%;
	box-sizing: border-box;
}


@media only screen and (max-width: 1120px) {
	#header .total .inner ul {
		display: flex;
		flex-direction: column;
	}
	#header .total .inner ul li {
		margin-left: 0;
		margin-bottom: 60px;
		text-align: center;
	}
	#header .total .inner ul li:last-child {
		margin-bottom: 0;
	}
	#header .total .inner ul li a {
		font-size: 1.2em;
		font-weight: 400;
	}
}

/* main slider */
.main_slider {
	position: relative;
	height: 100vh;
	overflow-x: hidden;
}
.main_slider .swiper-container {
	width: 100%;
	height: 100%;
}
.main_slider .swiper-container .swiper-slide {
	display: flex;
	justify-content: center;
	position: relative;
	align-items: center;
	text-align: center;
	font-size: 18px;
	background: #fff;
}
.main_slider .swiper-container .swiper-slide::before {
	content: "";
	position: absolute;
	width: 100%;
	height: 100%;
	z-index: 99;
	background-color: rgba(0,0,0,.75);
}
.main_slider .swiper-container .swiper-slide img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.main_slider .swiper-container .text_box {
	position: absolute;
	left: 50%;
	top: 50%;
	z-index: 100;
	transform: translate(-50%,-50%);
}
.main_slider .swiper-container .text_box .title {
	margin-bottom: 20px;
	line-height: 1.25;
	font-family: 'Montserrat', sans-serif;
	font-size: 3.5vw;
	color: #09f;
	opacity: 0;
	transform: translateY(30px);
}
.main_slider .swiper-container .text_box .title.active {
	transition: all 0.7s ease;
	opacity: 1;
	transform: translateY(0);
}
.main_slider .swiper-container .text_box p.text {
	line-height: 1.5;
	font-family: 'Montserrat', sans-serif;
	padding-top: 10px;
	color: rgba(255,255,255,.4);
	opacity: 0;
	transform: translateY(30px);
	transition: all 0.7s ease-in;
}
.main_slider .swiper-container .text_box p.text.active {
	opacity: 1;
	transform: translateY(0);
	transition-delay: 0.4s;
}

.main_slider .direction a.prev {
	display: block;
	position: absolute;
	left: 5%;
	top: 50%;
	z-index: 10;
	width: 150px;
	height: 150px;
	transform: translateY(-50%);
	transition: 0.3s;
}
.main_slider .direction a.next {
	display: block;
	position: absolute;
	right: 5%;
	top: 50%;
	z-index: 10;
	width: 150px;
	height: 150px;
	transform: translateY(-50%);
	transition: 0.3s;
}
.main_slider .direction a p {
	position: absolute;
	left: 50%;
	top: 50%;
	font-size: 0.875em;
	font-weight: 600;
	color: #fff;
	transform: translate(-50%,-50%);
}
.main_slider .direction span.prev_top {
	position: absolute;
	left: 0;
	top: 30%;
	width: 4px;
	height: 35px;
	background-color: #fff;
	transition: 0.3s;
}
.main_slider .direction span.prev_bottom {
	position: absolute;
	left: 0;
	top: 50%;
	width: 4px;
	height: 35px;
	background-color: #fff;
	transition: 0.3s;
}
.main_slider .direction span.next_top {
	position: absolute;
	right: 0;
	top: 30%;
	width: 4px;
	height: 35px;
	background-color: #fff;
	transition: 0.3s;
}
.main_slider .direction span.next_bottom {
	position: absolute;
	right: 0;
	top: 50%;
	width: 4px;
	height: 35px;
	background-color: #fff;
	transition: 0.3s;
}
.main_slider .direction a.prev:hover span.prev_top {
	top: 33%;
	height: 31px;
	transform: rotate(45deg);
}
.main_slider .direction a.prev:hover span.prev_bottom {
	top: 47%;
	height: 31px;
	transform: rotate(-45deg);
}
.main_slider .direction a.next:hover span.next_top {
	top: 33%;
	height: 31px;
	transform: rotate(-45deg);
}
.main_slider .direction a.next:hover span.next_bottom {
	top: 47%;
	height: 31px;
	transform: rotate(45deg);
}

.main_slider .controller {
	position: absolute;
	left: calc(8% + 150px);
	bottom: 10%;
	display: flex;
	align-items: center;
	transform: translateX(-50%);
	z-index: 1000;
}
.main_slider .controller .progressbar {
	position: relative;
	/* display: inline-block; */
	margin-left: 12px;
	width: 150px;
	height: 3px;
	background-color: #7e7575;
	z-index: 1000;
}
.main_slider .controller .progressbar span {
	position: absolute;
	left: 0;
	width: 0;
	height: 3px;
	background-color: #fff;
}
#pause_play {
	display: block;
	width: 20px;
	height: 20px;
	overflow: hidden;
}
#pause_play.pause {
	background: url(./ico/btn_pause.png) no-repeat 0 0;
}
#pause_play.play {
	background: url(./ico/btn_play.png) no-repeat 0 0;
}
.main_slider .account {
	position: absolute;
	left: 8%;
	bottom: 10%;
	z-index: 1000;
	font-size: 1.25em;
	color: #fff;
}
.main_slider .account .current {
	font-weight: 600;
}

@media only screen and (max-width: 1120px) {
	.main_slider .swiper-container .text_box .title {
		font-size: 36px;
	}
}
@media only screen and (max-width: 720px) {
	.main_slider .direction a p {
		display: none;
	}
}

/*About*/
#about {
	padding: 160px 20%;
	position: relative;
}
#about .bg {
	width: 100%;
	/* background: url(./images/contact_bg.jpg) no-repeat 0 0; */
	/* background-size: cover; */
	opacity: 0;
	transform: translateY(20px);
	transition: all 1.2s ease-in-out;
}
#about .bg.active {
	opacity: 1;
	transform: translateY(0);
}
#about .line {
	position: absolute;
	left: 50%;
	bottom: 60%;
	transform: translateX(-50%);
	height: 80px;
	width: 1px;
	background-color: #666;
	opacity: 0;
	transform: translateY(40px);
	transition: all 0.8s ease-in-out;
}
#about .line.active {
	opacity: 1;
	transform: translateY(0);
	transition-delay: 0.7s;
}
#about .texts {
	position: absolute;
	left: 50%;
	bottom: 0;
	width: 80%;
	transform: translateX(-50%);
	text-align: center;
}
#about h2 {
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	line-height: 2;
	font-family: 'Montserrat', sans-serif;
	font-size: 4vw;
	font-weight: 200;
	color: #888;
	letter-spacing: 2px;
	opacity: 0;
	transform: translateY(20px);
	transition: all 0.8s ease-in-out;
}
#about h2.active {
	opacity: 1;
	transform: translateY(0);
	transition-delay: 0.9s;
}
#about p {
	display: block;
	padding: 20px 0 10px;
	line-height: 1.8;
	font-family: 'Noto Sans KR';
	font-size: 1em;
	color: #888;
    font-weight: 300;
	opacity: 0;
	transform: translateY(20px);
	transition: all 0.8s ease-in-out;
}
#about p.active {
	opacity: 1;
	transform: translateY(0);
	transition-delay: 1.3s;
}

@media only screen and (max-width: 1120px) {
	#about h2 {
		font-size: 36px;
	}
}
@media only screen and (max-width: 420px) {
	#about p br {
		display: none;
	}
}

/* section */
#section {
	padding-top: 90px;
}
#section h2 {
	text-align: center;
}
#section .page {
	display: flex;
	padding: 40px 20% 40px;
	border-bottom: 1px solid rgba(255,255,255,.15);
	background-color: transparent;
	transition: background-color 0.3s;
	cursor: pointer;
}
#section .page:hover {
	background-color: #000;
}
#section .page:first-child {
	border-top: 1px solid rgba(255,255,255,.15);
}
#section .page .spec {
	padding-left: 50px;
}
#section .page .number {
	font-family: 'Montserrat-Thin';
    font-size: 1.125em;
    color: #555;
	font-weight: 400;
    line-height: 36px;
}
#section .page .spec p.title {
    vertical-align: middle;
	font-family: 'Montserrat', sans-serif;
    font-size: 1.5em;
    font-weight: 900;
    line-height: 40px;
    color: #fff;
}
#section .page:hover .spec p.title {
	color: #09f;
}
#section .page .spec span {
	display: block;
	padding-top: 10px;
	line-height: 1.8;
	font-family: 'Noto Sans KR';
    font-size: 0.875em;
    font-weight: 400;
    color: #999;
}

/* topBtn */
.top_btn {
    position: absolute;
    right: 5%;
    bottom: 15%;
}
.top_btn a {
    display: block;
    width: 20px;
    height: 26px;
	background: url(./images/m_topBtn.png) no-repeat 0 0;
	background-size: cover;
}

/* Contact */
#contact {
	position: relative;
    padding: 100px 0;
    width: 100%;
    background-color: #000;
}
#contact .ft_inner {
    margin: 0 auto;
    max-width: 1400px;
    display: flex;
	align-items: center;
}
#contact .ft_inner .ft_left {
	padding-left: 10%;
}
#contact .ft_inner .ft_left h3 {
	line-height: 1.4;
    font-size: 2.5vw;
    font-weight: bold;
    color: #666;
    text-align: center;
}
#contact .ft_inner .ft_right {
	padding-left: 10%;
}
#contact .ft_inner .ft_right ul {
    justify-content: space-around;
    padding: 10px 0;
}
#contact .ft_inner .ft_right li {
	display: inline-block;
}
#contact .ft_inner .ft_right li p {
	display: inline-block;
    font-size: 16px;
    font-weight: bold;
	color: #555;
}
#contact .ft_inner .ft_right li span {
	display: inline-block;
    font-size: 14px;
    padding: 0 10px;
	color: #ccc;
}
#contact .ft_inner .ft_right p {
	padding: 10px;
	color: #ccc;
	line-height: 1.5;
	font-size: 14px;
}
#contact .ft_inner .ft_right p.name {
	display: inline-block;
    font-size: 14px;
    font-weight: bold;
    text-align: right;
}
#contact .ft_inner .ft_right p.copyright {
    display: inline-block;
    font-size: 14px;
    font-weight: bold;
    text-align: right;
    color: #09f;
}

@media only screen and (max-width: 1120px) {
	#contact .ft_inner .ft_left h3 {
		font-size: 1.5em;
	}
}
@media only screen and (max-width: 920px) {
	#contact .ft_inner .ft_left {
		display: none;
	}
	#contact .ft_inner .ft_right {
		padding: 0 30px;
	}
}